import React from 'react';
import { PageHeader, Button } from 'antd';
import { Link, withRouter } from 'react-router-dom';

import { isLoggedIn } from '../../services/AuthService';
import { getCurrentUser } from '../../services/ApiService';
import * as RouteConstants from '../../constants/RouteConstants';

class Header extends React.Component {

  constructor (props) {
    super(props)

    this.state = { name: '' }
    this.state = { photo: '' }
    this.state = { isFetchingUser: false }
  }

  componentDidMount () {
    if (isLoggedIn()) {
      getCurrentUser()
        .then((res) => {
          this.setState({
            isFetchingUser: true,
            photo: res.data.images[0].url,
            name: res.data.display_name
          });
        })
    }
  }

  render () {
    return (
      <PageHeader
    title="2 Spotify"
    subTitle = {this.state.isFetchingUser ? `Hello, ${this.state.name}!` : 'Hello, friend!'}
    extra={[
      <Button key={RouteConstants.HOME} style={{marginTop: '6px'}}><Link to={RouteConstants.HOME}>Home</Link></Button>,
      <Button key={RouteConstants.APPlE} style={{marginTop: '6px'}}><Link to={RouteConstants.APPlE}>Apple Music</Link></Button>,
      <Button key={RouteConstants.VK} style={{marginTop: '6px'}}><Link to={RouteConstants.VK}>Boom (VK)</Link></Button>,
      <Button key={RouteConstants.YANDEX} style={{marginTop: '6px'}}><Link to={RouteConstants.YANDEX}>Yandex</Link></Button>
    ]}
    avatar={ this.state.isFetchingUser ? {src: this.state.photo} : null}
    ></PageHeader>
    );
  }
}

export default withRouter(Header);